___CSS_LOADER_EXPORT___.push([module.id, `footer {
  padding: 1rem;
  justify-content: center;
  align-items: center;
  min-width: 100%;
  background-color: var(--primary-color);
  display: flex;
  color: var(--text-color); /* Updated */
}

#footer-container {
  max-width: 30%;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  color: var(--text-color); /* Updated */
}

#footer-info {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin-bottom: 2rem;
  color: var(--text-color); /* Updated */
}

#footer-name {
  font-size: 2rem;
  margin-bottom: 1rem;
  color: var(--text-color); /* Updated */
}

#footer-desc,
#footer-address,
#footer-phone,
#footer-email {
  margin: 0.5rem 0;
  color: var(--text-color); /* Updated */
}

#footer-socials {
  align-items: center;
  justify-content: center;
  display: flex;
  gap: 1rem;
  margin-top: 1rem;
  color: var(--text-color); /* Updated */
}

#socials a {
  color: var(--text-color); /* Updated */
  text-decoration: none;
  font-size: 1.5rem;
}

#footer-img {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  margin-top: 2rem;
}
